export default {
  data() {
    return {
      datalist: [],
      isShow: false,
      searchvalue:''
    }
  },
  props: {
    itemlist: Object, //父组件传来的数据
    placeholder: {
      type: String,
      default: '搜索', //input placeholder的默认值
    },
    isNeedSearch: {
      //是否需要搜索框
      type: Boolean,
      default: false,
    },
    nodatatext: {
      type: String,
      default: '未找到结果', //没有搜索到时的文本提示
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.datalist = this.itemlist.data
      //点击组件以外的地方，收起
        document.documentElement.addEventListener(
        'click',
        (e) => {
            if (!this.$el.contains(e.target)) {
            this.isShow = false
            }
        },
        false
        )
    })
  },
  methods: {
    selectToggle(data) {
      this.itemlist.cur.label = data.label
      this.isShow = false
      this.$emit('item-click', data)
    },
    clearValue(){
        this.searchvalue = '';
        this.datalist = this.itemlist.data.filter((item, index, arr) => {
        return item.label.indexOf(this.searchvalue) != -1
      })
    },
    search(e) {
      this.searchvalue = e.currentTarget.value
      this.datalist = this.itemlist.data.filter((item, index, arr) => {
        return item.label.indexOf(this.searchvalue) != -1
      })
    },
  },
}
